@import url(https://fonts.googleapis.com/css2?family=Monoton&family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to left, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%);
  /* 百分数指的是那个颜色从屏幕的什么位置开始，0%指的是从屏幕的最左侧开始，100%指的是屏幕的最右侧 */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* special elite, squada one, monoton, press start 2p */

h1 {
    font-family: 'Monoton', cursive;
    font-weight: 200;
    color: #0ccac4;
}

.card {
    font-family: 'Press Start 2P', cursive;
    font-size: 0.1em;
}
